//  Login  - Login.jsx
import { useEffect, useState, useContext } from 'react';
import { GP } from '../module/Contexter';
import { useNavigate } from 'react-router-dom';
import SubTop from '../module/SubTop';
import login from '../../css/page/login.scss';
import member_data from '../../js/data/member_data.json';

//     ( 1)
if (!localStorage.getItem('member_data')) {
  localStorage.setItem('member_data', JSON.stringify(member_data));
}
//  
const PHONE_REGEX = /^01[016789]-?\d{3,4}-?\d{4}$/;
const PW_REGEX =
  /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*?&#+\^])[A-Za-z\d@$!%*?&#+\^]{8,16}$/;

function Login({ gnb1, gnb2 }) {
  const context = useContext(GP);
  const navigate = useNavigate();
  const [phoneNum, setPhoneNum] = useState('');
  const [phoneValid, setphoneValid] = useState(false);
  const [pw, setPw] = useState('');
  const [pwValid, setpwValid] = useState(false);
  const [showPw, setShowPw] = useState(false);
  const [notAllow, setNotAllow] = useState(true);

  //  
  // loginState boolean   
  const loginState = context.loginState.isLogin;
  //      null 
  const user = loginState ? context.user : null;
  //      null 
  const userName = user !== null ? user.name : null;

  //    
  const handlePhoneNumber = (e) => {
    const value = e.target.value;
    setPhoneNum(value);
    // 'admin'      
    if (value === 'admin') {
      setNotAllow(false);
    } else {
      setNotAllow(!(PHONE_REGEX.test(value) && PW_REGEX.test(pw)));
    }
  };

  //   
  const handlePw = (e) => {
    const value = e.target.value;
    setPw(value);
    setNotAllow(!(PHONE_REGEX.test(phoneNum) && PW_REGEX.test(value)));
  };

  //   
  const onClickConfirmButton = () => {
    const cleanPhoneNum = phoneNum.replace(/\D/g, ''); //  
    const member_data = JSON.parse(localStorage.getItem('member_data')) || []; //   

    if (phoneNum === 'admin') {
      // admin      
      const adminUser = {
        name: "",
        id: 1,
        pw, //      !    
        bData: '',
        iLoveIt: '',
        profileImage: '/img/sub/img-profile-temp.png',
        currentData: [],
      }; // admin  

      sessionStorage.setItem('loggedInUser', JSON.stringify(adminUser));
      alert('  ');
      navigate('/'); //     
      context.setLogin(true);
      return;
    }

    const user = member_data.find(
      (m) => String(m.id).replace(/\D/g, '') == cleanPhoneNum && m.pw === pw
    );
    
    if (user) {
      //       
      sessionStorage.setItem('loggedInUser', JSON.stringify(user));
      alert(' ');
      console.clear();
      navigate('/'); //     
      context.setLogin(true);
    } else {
      alert('   .');
    }
  };
  
  //  ,       
  useEffect(() => {
    if (phoneValid && pwValid) {
      setNotAllow(false);
      return;
    } // if //
    setNotAllow(true);
  }, [phoneValid, pwValid]);

  //   
  const handleJoinClick = () => {
    navigate("/join");
  }
  return (
    <>
      <SubTop gnb1={gnb1} gnb2={gnb2} />
      <div className='page-cover'>
        <div className='login-container' id='login'>
          <section className='login-wrap'>
            <div className='input-wrap'>
              <input
                type='text'
                className='input-box'
                placeholder=' '
                value={phoneNum}
                onChange={handlePhoneNumber}
                onKeyUp={(e) => {
                  if (e.key === 'Enter') {
                    document.querySelector('.login-btn').click();
                  }
                }}
              />
              <div className='errorMessageWrap'>
                {!PHONE_REGEX.test(phoneNum) && phoneNum.length > 0 && (
                  <div>  .</div>
                )}
              </div>
              <div className='password-wrap'>
                <input
                  type={showPw ? 'text' : 'password'} //  /
                  className='input-box'
                  placeholder=''
                  value={pw}
                  onChange={handlePw}
                  onKeyUp={(e) => {
                    if (e.key === 'Enter') {
                      document.querySelector('.login-btn').click();
                    }
                  }}
                />{' '}
                <span
                  className='toggle-password'
                  onClick={() => setShowPw(!showPw)}
                >
                  {showPw ? '' : ''}{' '}
                  {/*   /   */}
                </span>
              </div>
              <div className='errorMessageWrap'>
                {!PW_REGEX.test(pw) && pw.length > 0 && (
                  <div>  .</div>
                )}
              </div>
            </div>
            <button
              onClick={onClickConfirmButton}
              disabled={notAllow}
              className='login-btn'
            >
              
            </button>
            <hr className='divider' />
            <button className='join-btn' onClick={handleJoinClick}></button>
          </section>
        </div>
      </div>
    </>
  );
}
export default Login;
