//  Monthly  - Monthly.jsx

import SubTop from "../module/SubTop";

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";

//  CSS  ////
import "../../css/page/monthly.scss";

//   /////
import { monthlyData } from "../../js/data/monthly_data";
import { useState } from "react";

function Monthly({ gnb1, gnb2 }) {
  //   !
  //   
  const [seq, setSeq] = useState(0);
  const selData = monthlyData[seq];

  return (
    <>
      <SubTop gnb1={gnb1} gnb2={gnb2} />
      <div className="contents">
        <div className="monthly-recommend">
          <div className="book-swiper">
            <Swiper
              loop={true}
              spaceBetween={20}
              slidesPerView={1.5}
              centeredSlides={true}
              breakpoints={{
                500: { slidesPerView: 3, centeredSlides: true },
                768: { slidesPerView: 3, centeredSlides: true },
                1024: { slidesPerView: 5, centeredSlides: true },
              }}
              //    ////
              onSlideChange={(me) => {
                console.log("Slide change!!!", me.realIndex);
                //    
                //    
                setSeq(me.realIndex);
              }}
            >
              {monthlyData.map((v, i) => (
                <SwiperSlide key={i}>
                  <img
                    src={"../img/monthly/img-" + v.bImg + ".jpg"}
                    alt=" "
                  />
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
        </div>
        <div className="book-info-box">
          <div className="tit">{selData.bName}</div>
          <div className="info-wrap">
            <div className="left">
              <div className="sub-tit"> </div>
              <div className="author"> : {selData.bAuthor}</div>
              <div className="publ"> : {selData.bPublisher}</div>
              <div className="genre"> : {selData.bGenre}</div>
            </div>
            <div className="right">
              <div className="sub-tit"></div>
              <div className="sub-info">{selData.bDescription}</div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Monthly;
