import React, { useEffect, useLayoutEffect } from "react";
import ReactDOM from "react-dom/client";
import { BrowserRouter, Routes, Route, useLocation } from "react-router-dom";
import Layout from "./components/layout/Layout";

//  JS
import "./css/common/_core.scss";
import Main from "./components/page/Main";
import Book from "./components/page/Book";
import Community from "./components/page/Community";
import Error from "./components/page/Error";
import Join from "./components/page/Join";
import Mypage from "./components/page/Mypage";
import Login from "./components/page/Login";
import BookSearch from "./components/page/BookSearch";
import Monthly from "./components/page/Monthly";
import BookList from "./components/page/BookList";
import BookDetail from "./components/page/BookDetail";
import Article from "./components/page/Article";
import Post from "./components/page/Post";
import TotalSearch from "./components/page/TotalSearch";

//  css
import "./css/common/_core.scss";

export default function MainComponent() {
  return (
    <BrowserRouter basename="/CCBB">
      <ScrollTop />
      <Routes>
        <Route path="/" element={<Layout />}>
          {/*   */}
          <Route index element={<Main />} />
          {/*   */}
          <Route path="/apply/book" element={<Book gnb1="" gnb2="" />} />
          {/*    */}
          <Route path="community/notice" element={<Community gnb1="" gnb2="" data="notice" />} />
          {/*     */}
          <Route path="community/notice/search/:results" element={<Community gnb1="" gnb2="" data="notice" />} />
          {/*     */}
          <Route path="community/notice/:id" element={<Article gnb1="" gnb2="" />} />
          {/*     */}
          <Route path="community/notice/post" element={<Post gnb1="" gnb2="" data="notice" />} />
          {/*  faq  */}
          <Route path="community/faq" element={<Community gnb1="" gnb2="FAQ" data="faq" />} />
          {/*  faq  */}
          <Route path="community/faq/search/:results" element={<Community gnb1="" gnb2="FAQ" data="faq" />} />
          {/*  faq   */}
          <Route path="community/faq/post" element={<Post gnb1="" gnb2="FAQ" data="faq" />} />
          {/*    */}
          <Route path="community/freeboard" element={<Community gnb1="" gnb2="" data="freeboard" />} />
          {/*    */}
          <Route path="community/freeboard/search/:results" element={<Community gnb1="" gnb2="" data="freeboard" />} />
          {/*    */}
          <Route path="community/freeboard/:id" element={<Article gnb1="" gnb2="" data="freeboard" />} />
          {/*    */}
          <Route path="community/freeboard/post" element={<Post gnb1="" gnb2="" data="post" />} />
          {/*  */}
          <Route path="join" element={<Join gnb1="" gnb2="" />} />
          {/*  */}
          <Route path="login" element={<Login gnb1="" gnb2="" />} />
          {/*  */}
          <Route path="mypage" element={<Mypage gnb1="" gnb2="" />} />
          {/*  */}
          <Route path="search/booksearch" element={<BookSearch gnb1="" gnb2="" />} />
          {/*   */}
          <Route path="search/booksearch/:results" element={<BookSearch gnb1="" gnb2="" />} />
          {/*  */}
          <Route path="search/best" element={<BookList gnb1="" gnb2="" />} />
          {/*  */}
          <Route path="search/new" element={<BookList gnb1="" gnb2="" />} />
          {/*   */}
          <Route path="monthly/recommend" element={<Monthly gnb1="" gnb2="  " />} />
          {/*   */}
          <Route path="book/:isbn" element={<BookDetail />} /> {/*    */}
          {/*  */}
          <Route path="totalsearch" element={<TotalSearch />} />
          {/*   */}
          <Route path="totalsearch/:results" element={<TotalSearch />} />
          {/*  */}
          <Route path="error" element={<Error />} />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}
const ScrollTop = () => {
  const { pathname } = useLocation();

  useLayoutEffect(() => {
    window.scrollTo(0, 0);
    // console.log(":", pathname);
  }, [pathname]);
  return null;
};

const wrapper = ReactDOM.createRoot(document.querySelector(".wrapper"));
wrapper.render(<MainComponent />);
